<template>
    <div class="product-list">
        <cube-scroll>
        <ul class="list-wrapper">
            <li v-for="(item, index) in products" class="list-item" :key="index">
                <product :item="item"></product>
            </li>
            <li v-if="products.length === 0" class="no-list">
                暂无商品~
            </li>
        </ul>
        </cube-scroll>        
    </div>
</template>
<script>
export default {
    data() {
        return {
        }
    },
    props: {
        products: {
            type: Array,
            default() {
                return []
            },   
        }
    },
    created() {
    },
    components: {
    },
    methods: {
    }
}
</script>
<style lang="stylus" scoped>
.product-list {
    height: calc(100vh - 248px);
    .list-wrapper {
        background-color: #FFFFFF;
        padding: 0 30px;
        .no-list {
            height: 80px;
            line-height 80px;
            text-align: center;
            font-size: 28px;
        }
    }
}
</style>


