<template>
    <div class="category-select">
        <div class="search-wrap">
			<div class="search" @click="_onSearch">
				<i class="search-i"></i>
				<span class="search-text"></span>
			</div>
		</div>
        <cube-tab-bar class="tab-nav" v-model="selectedLabel"
            show-slider
            :use-transition="disabled"
            ref="tabNav"
            @click="_choseClass"
            :data="tabLabels">
        </cube-tab-bar>
        <cube-tab-bar class="tab-child-nav" v-if="renderDom" v-model="selectedChildLabel"
                :show-slider="false"
                :use-transition="disabled"
                ref="tabNav"
                @click="_choseItem"
                :data="tabChildLabels">
        </cube-tab-bar>
        <product-list ref="productList" :products="products"></product-list>
    </div>
</template>
<script>
import { queryItemList } from "@/api/product.js";
import { itemClassifyList } from '@/utils/generic.js';
import ProductList from './component/product-list';
export default {
    data() {
        return {
            selectedLabel: 'all',
            disabled: true,
            tabLabels: [],
            selectedChildLabel: '全部',
            tabChildLabels: [],
            renderDom: false,
            productLists: {},
            products: [],
        }
    },
    components: {
        ProductList,
    },
    created() {
        this.selectedLabel = Number(this.$route.query.classifyId);
        itemClassifyList.forEach(item => {
            this.tabLabels.push({
                label: item.classifyName,
                value: item.classifyId,
            })
        })
    },
    mounted() {
        this._queryItemList(this.$route.query.classifyId)
    },
    methods: {
        // 搜索
        _onSearch() {
            this.$router.push({ path: "/search" });
        },
        _queryItemList(classifyId) {
            let params = {
                classifyId,
                userId: 1,
            }
            queryItemList(params).then(res => {
                this.tabChildLabels = [];
                Object.keys(res).forEach((key) => {
                    this.tabChildLabels.push({
                        label: key
                    })
                });
                this.productLists = res;
                this.selectedChildLabel = '全部';
                this.products = this.productLists['全部'];
                this.renderDom = true;
            }).catch(err => {

            })
        },
        _choseClass(value) {
            this._queryItemList(value);
        },
        _choseItem(value) {
            this.products = this.productLists[value];
        },
    }
}
</script>
<style lang="stylus" scoped>
.category-select {
    font-family:HiraginoSansGB-W3;
    background: #F2F2F2;
    width: 100%;
    height: 100%;
    .search-wrap {
		background-color: #ffffff;
		padding: 12px 0 16px 0;
		.search {
			margin: 0 auto;
			width: 700px;
			height: 60px;
			line-height: 60px;
			background: rgba(242,242,242,1);
			border-radius: 30px;
			text-align: center;
            padding-left: 24px;
			.search-i {
				vertical-align: middle;
				display: inline-block;
				width: 32px;
				height: 32px;
				background-image: url(../../../static/image/common/icon_search@2x.png);
				background-size: 100% 100%;
				background-repeat: no-repeat;
			}
			.search-text {
				display: inline-block;
				font-size: 24px;
				color: rgba(102,102,102,1);
			}
		}
	}
    .tab-nav {
        height: 80px;
    }
    .tab-child-nav {
        height: 80px;
        background-color: #ffffff;
    }
}
</style>


